import { Component } from "react"

export default class Card extends Component {

    constructor(props) {
        super(props)

        this.state = {
            cardContent: null,
            currentTab: null
        }
    }

    selectTab(index) {
        this.setState({
            cardContent: this.props.tabs[index].content,
            currentTab: index
        })
    }

    render() {
        let tabs = ''
        if ( this.props.tabs ) {
            tabs = Object.keys(this.props.tabs).map((index) => {
                return (
                    <span key={`tab_${index}${Date.now()}`} className={`tab ${( index == this.state.currentTab ) ? "active" : ""}`} onClick={() => { this.selectTab(index) }}>{ this.props.tabs[index].title }</span>
                )
            })
        }
    
        return (
            <section className="rounded-2xl bg-white overflow-hidden shadow-lg">
                <div className="card-header bg-gray-100 px-4 py-2 flex justify-between items-center">
                    <h2>{this.props.title}</h2>
                    { this.props.closeAction ? (
                        <span className="font-bold text-base cursor-pointer" onClick={() => {
                            this.props.closeAction()
                            this.setState({
                                cardContent: null,
                                currentTab: null
                            })
                        }}>x</span>
                    ) : (
                        ''
                    ) }
                </div>
                <div className="card-body border-t-2 border-b-2 max-h-96 overflow-y-auto">
                    {this.state.cardContent}
                </div>
                <div className="card-footer bg-gray-100 p-4 flex justify-evenly">
                    {tabs}
                </div>
            </section>
        )
    }
}