import { Component, createRef } from "react"
import L, { LatLng, LatLngBounds } from 'leaflet'

import Card from "../components/Card"
import Map from "../components/Map"


import washroomAm from './../assets/images/washroomAm.svg';
import playgroundAm from './../assets/images/playgroundAm.svg';
import AmenitiesCard from "../components/AmenitiesCard";
import Gallery from "../components/Gallery";

const imageFolder = require.context('./../assets/images/focus', true)

export default class Main extends Component {

    constructor(props) {
        super(props)

        this.state = {
            isPointOfInterestSelected: false,
            pointOfInterest: {
                title: '',
                description: '',
                hours: '',
                coordinates: {
                    lat: 0,
                    lng: 0
                },
                images: [],
                amenities: []
            },
            pointOfInterestMenu: false, // true = open, false = close
            mapObject: false,
            preload: ( props.preload ) ? true : false
        }

        this.cardRef = createRef()
    }

    focusOn(point) {

        if ( this.state.mapObject ) {
            this.state.mapObject.setView({
                lat: point.coordinates.lat,
                lng: point.coordinates.lng
            }, point.coordinates.zoom, {
                "animate": true,
                "pan": {
                    "duration": 1
                }
            })

            this.showOnMap(point.coordinates.lat, point.coordinates.lng, "You Are Here!")
        }
        
        
        this.setState({
            isPointOfInterestSelected: true,
            pointOfInterest: {
                title: point.title,
                description: point.description,
                coordinates: {
                    lat: point.coordinates.lat,
                    lng: point.coordinates.lng,
                },
                images: point.images,
                amenities: point.amenities
            }
        })

        if ( this.cardRef.current ) {
            this.cardRef.current.setState({
                currentTab: 0,
                cardContent: this.generateInfoTab(point.description, point.images)
            })
        }
    }

    togglePointOfInterestMenu() {
        this.setState({
            pointOfInterestMenu: ( this.state.pointOfInterestMenu ) ? false : true
        })
    }

    getMapObject(obj) {
        this.setState({
            mapObject: obj
        })
    }

    generateInfoTab(description, imageList) {
        let images = []

        if ( imageList ) {
            imageList.forEach(image => {
                let imgSrc = imageFolder(`./${image}`)
                images.push(imgSrc.default)
            })
        }

        return ( 
            <div className="p-4">
                <h3 className="text-lg font-medium uppercase tracking-widest mb-2">About</h3>
                <div className="flex">
                    <Gallery images={images} variant="flex" />
                </div>
                {description}
            </div>
        )
    }

    showOnMap(lat, lng, text = '') {
        L.popup({
            autoClose: false,
            className: "popup-theme",
            closeButton: false,
            closeOnClick: false,
            permanent: false
        }).setLatLng({
            lat: lat,
            lng: lng
        }).setContent(`
        <h1 class="font-bold uppercase tracking-widest">${text}</h1>
        `).openOn(this.state.mapObject)
    }

    componentDidMount() {

        if ( this.props.match && this.props.match.params && this.props.match.params.id ) {

            this.props.points.forEach(point => {
                if ( point.id == this.props.match.params.id ) {
                    this.focusOn(point)
                }
            })
        }
    }

    render () {

        if ( this.state.preload ) {
            if ( this.state.isPointOfInterestSelected ) {
                console.log(this.state)
            }
        }
        

        let pointsOfInterest = Object.keys(this.props.points).map((index) => {
            let point = this.props.points[index]
            return (
                <div key={`poi_${index}`} className="bg-white p-4 shadow-md rounded-xl cursor-pointer" onClick={() => { this.focusOn(point) }} >{point.title}</div>
            )
        })
        
        let amenities = false

        if ( this.state.pointOfInterest.amenities ) {
            amenities = this.state.pointOfInterest.amenities.map((amenity, index) => {
                return ( <AmenitiesCard key={`amenity_${index}`} title={amenity.title} subtitle="8 points" thumbnail={playgroundAm} clicked={(arg) => {
                    this.showOnMap(amenity.coordinates.lat, amenity.coordinates.lng, amenity.title)
                }} /> )
            })
        }

        let tabs = []

        tabs.push({
            title: "Info",
            content: this.generateInfoTab(this.state.pointOfInterest.description, this.state.pointOfInterest.images)
            })

        if ( amenities ) {
            tabs.push({
                title: "Amenities",
                content: (
                    <div className="flex-wrap bg-white p-5 card-wrapper">
                        { amenities }
                    </div>
                )
                })
        }


        return (
            <>
                <div className="map-wrapper fixed top-0 left-0 w-full h-full m-0 p-0 z-0">
                    <Map getMapObject={(obj) => {this.getMapObject(obj)}} showOnMap={this.showOnMap} preload={this.state}  />
                </div>
                <div className={`fixed bottom-0 left-0 w-full max-h-full p-5 md:bottom-auto md:max-w-md md:left-auto md:right-0 md:top-0 component ${this.state.isPointOfInterestSelected ? '' : 'component-hidden'}`}>
                    <Card ref={this.cardRef} title={this.state.pointOfInterest.title} tabs={tabs} closeAction={() => {
                        this.setState({isPointOfInterestSelected: false, pointOfInterestMenu: false})
                        this.state.mapObject.eachLayer(layer => {
                            if ( layer.options.permanent == false ) {
                                this.state.mapObject.removeLayer(layer)
                            }
                        })
                        }} />
                </div>
                <div className={`fixed bottom-0 left-0 w-full max-h-full p-5 md:bottom-auto md:max-w-md md:left-auto md:right-0 md:top-0 component ${(!this.state.isPointOfInterestSelected && !this.state.pointOfInterestMenu) ? '' : 'component-hidden'}`}>
                    <div className="flex flex-row bg-white shadow-lg rounded-2xl transition uppercase tracking-widest text-gray-600 items-end">
                        <a href="#" className="block w-full p-5" onClick={() => { this.togglePointOfInterestMenu() }}>List of Points</a>
                    </div>
                </div>
                <div className={`fixed bottom-0 left-0 w-full max-h-full p-5 md:bottom-auto md:max-w-md md:left-auto md:right-0 md:top-0 component ${(!this.state.isPointOfInterestSelected && this.state.pointOfInterestMenu) ? '' : 'component-hidden'}`}>
                    <div className="grid grid-cols-1 gap-4 rounded-xl overflow-hidden bg-gray-100 p-4">
                        { pointsOfInterest }
                    </div>
                </div>

            </>
        )
    }
}