const AmenitiesCard = (props) => {

    return (
        <div className="w-1/2 px-2" onClick={props.clicked.bind('msg')}>
            <div className="flex flex-col justify-center items-center text-center h-40 w-full shadow-lg overflow-hidden rounded-2xl py-3 px-6 mb-4 bg-gray-100">
                <div className="card-thumbnail w-10 bg-center bg-contain bg-no-repeat" style={{backgroundImage: `url(${props.thumbnail})`}}></div>
                <div className="card-info pt-3">
                    <h3 className="tracking-wide font-medium">{props.title}</h3>
                    <p className="tracking-wide text-sm text-gray-400">{(props.subtitle) ? props.subtitle : '-' }</p>
                </div>
            </div>
        </div>
    )
}

export default AmenitiesCard