import waterfrontlogo from './../assets/images/waterfront-logo.png'
import loadinganimation from './../assets/images/loading-animation.svg'

const LoadingScreen = () => {

    return (
        <main>
            <div className="h-screen flex flex-col justify-center p-4">
                <img src={waterfrontlogo} alt="waterfront logo" className="w-md mx-auto max-w-full"></img>
                <img className="pt-16 ml-auto mr-auto w-16" src={loadinganimation}></img>
            </div>
        </main>
    )
}

export default LoadingScreen